import "./Button.css"

export const createButton = ({
  size = 'small',
  backgroundColor = 'grey',
  label = "click me!",
  onClick,
}) => {
  const btn = document.createElement("button");
    btn.className = [`btn--${size}`];
    btn.style.backgroundColor = backgroundColor;
    btn.innerText = label;
    btn.addEventListener("click", onClick);

    return btn;
};